import React from 'react'
import styles from './expandable.module.scss'
import { FaPlus, FaMinus } from 'react-icons/fa'

class Expandable extends React.Component {
  constructor(props) {
    super(props)
    this.state = { }
  }
  toggle() {
    const expanded = !this.state.expanded
    this.setState({ expanded })
  }
  render() {
    const expanded = this.state.expanded || this.props.expanded;
    const { title, ariaLabel } = this.props
    return (
      <details>
        <summary className={styles.headingButton}>
          <h3 className={styles.heading}>
          {title}
          </h3>
        </summary>
        <div className={styles.content}>
        {this.props.children}
        </div>
      </details>
    )
  }
}


class InlineExpandable extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = { }
  }
  toggle() {
    const expanded = !this.state.expanded
    this.setState({ expanded })
  }
  render() {
    const expanded = this.state.expanded || this.props.expanded;
    const { title, ariaLabel } = this.props;
    return (
      <details>
        <summary className={`${styles.inlineHeadingButton} ${this.props.buttonClassName}`}>
          {title}
        </summary>
        <div className={styles.content}>{this.props.children}</div>
      </details>
    )
  }
}
export { Expandable, InlineExpandable }
